.button {
  width: 248px;
  height: 70px;

  background: #52519d;
  border-radius: 40px;
  border: none;
  font-family: Sora;
  font-style: normal;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.25px;
  color: #eeeeee;
  outline: none;
}

.button:hover {
  background: #eeeeee;
  color: #52519d;
  border: none;
  cursor: pointer;
}
.buttonsort {
  composes: button;
  background: transparent;
  color: #eeeeee;
}
.buttonsort:hover {
  border: none;
  cursor: pointer;
  background: transparent;
  color: #eeeeee;
}
.previous {
  composes: button;
  background: transparent;
  width: 105px;
  height: 30px;
  margin-left: 30px;
  margin-top: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: #eeeeee;
}
.previous:hover {
  background: transparent;
  color: #eeeeee;
}
.next {
  composes: button;
  width: 59px;
  height: 30px;
  background: transparent;
  margin-right: 30px;
  margin-top: 20px;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  color: #eeeeee;
}
.next:hover {
  background: transparent;
  color: #eeeeee;
}
