.mainnormaldiv {
  display: flex;
  flex-wrap: wrap;

  @media screen and (min-width: 320px) {
    margin-left: 150px;
    margin-right: 100px;
  }
  @media screen and (min-width: 768px) {
    margin-left: 25px;
    margin-right: 5px;
  }
  @media screen and (min-width: 1024px) {
    margin-left: 125px;
    margin-right: 105px;
  }
  @media screen and (min-width: 1440px) {
    margin-left: 325px;
    margin-right: 325px;
  }
  @media screen and (min-width: 1920px) {
    margin-left: 525px;
    margin-right: 525px;
  }
}
.mainsmalldiv {
  display: flex;
  flex-wrap: wrap;
  margin-left: 225px;
  margin-right: 1095px;
  margin-top: 135px;
  margin-bottom: 110px;
}
.mainnormal {
  border-radius: 40px;
  font-family: Sora;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 1.25;
  text-align: center;
  color: #eeeeee;
  background: #333348;
  text-decoration: none;
  margin-bottom: 30px;
  margin-right: 30px;
  width: 420px;
  height: 370px;
  flex-basis: calc((100% - 60px) / 2);
}
.mainsmall {
  border-radius: 40px;
  text-align: center;
  color: #eeeeee;
  background: #333348;
  text-decoration: none;
  margin-bottom: 30px;
  margin-right: 30px;
  width: 270px;
  height: 265px;
  flex-basis: calc((100% - 60px) / 2);
}

.footer {
  display: flex;
  justify-content: space-between;
  padding-left: 225px;
  padding-right: 225px;
  font-family: Sora;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 30px;
  color: #eeeeee;
}
.previos {
  display: flex;
  margin-top: 26px;
}
.modalList {
  margin-top: 108px;
  margin-bottom: 108px;
  margin-left: 150px;
  margin-right: 150px;
}
.modaltitle {
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 45px;
  color: #ffffff;
}
.modaltext {
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  color: #ffffff;
}
